import React, { Fragment, useEffect, useState } from 'react'
import { emphasize, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';

import clsx from 'clsx';

import { createRole, updateRole } from '../../api/api';

import { Button } from '@material-ui/core';
import TextField from "@material-ui/core/TextField";
import Swal from 'sweetalert2';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 250,
    minWidth: 290
  },
  input: {
    display: "flex",
    padding: 0,
    height: "auto"
  },
  valueContainer: {
    display: "flex",
    flexWrap: "wrap",
    flex: 1,
    alignItems: "center",
    overflow: "hidden"
  },
  margin: {
    marginTop: 10
  },
  chip: {
    margin: theme.spacing(0.5, 0.25)
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === "light"
        ? theme.palette.grey[300]
        : theme.palette.grey[700],
      0.08
    )
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2)
  },
  singleValue: {
    fontSize: 16
  },
  placeholder: {
    position: "absolute",
    left: 2,
    bottom: 6,
    fontSize: 16
  },

  divider: {
    height: theme.spacing(2)
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,

  },
  button:
  {
    margin: theme.spacing(1),
  },
  fab:
  {
    marginTop: -2,
    float: "right"
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  formControl: {
    margin: theme.spacing(1),
  },
}));


const RoleForm = props => {

  const [disabled, setDisabled] = useState(true)
  const [role, setRole] = useState(null)
  const classes = useStyles()
  const [values, setValues] = React.useState({
    roleName: '',
    saved: false
  })

  useEffect(() => {
    if (props.role) {
      console.log('props', props.role.name);
      setRole(props.role);
      setDisabled(false);
      setValues({ ...values, 'roleName': props.role.name })
    }
  }, []);

  const initialState = {
    roleName: '',
    saved: true
  }


  const inputChange = prop => event => {
    setValues({ ...values, [prop]: event.target.value })
    if (event.target.value.toString().trim().length > 5)
      setDisabled(false)
    else
      setDisabled(true)
  }

  const onSubmitRole = e => {

    let input = {
      name: values.roleName
    }

    if (!props.role) {
      createRole(input).then(response => {
        if (response.status === 'success') {
          props.handleClose()
          props.passRoleCreate(response.role)
          Swal.fire({
            icon: 'success',
            title: `El role ${values.roleName}  fue creado(a) exitosamente`,
            showConfirmButton: false,
            timer: 1500
          });

          setValues({ ...initialState })
        }
      });
    } else {
      input.id = props.role._id;
      updateRole(input).then(response => {
        if (response.status === 'success') {
          props.handleClose()

          response.role.name = values.roleName;
          props.passRoleEdit(response.role)

          Swal.fire({
            icon: 'success',
            title: `El role ${values.roleName}  fue actualizado exitosamente`,
            showConfirmButton: false,
            timer: 1500
          })

          setValues({ ...initialState });
        }
      });
    }
  }


  return (
    <Fragment>

      <Container maxWidth="md" className={classes.margin}>

        <Paper className={classes.paper}>
          <form onSubmit={e => {
            e.preventDefault()
            onSubmitRole()
          }} >
            <Grid container spacing={3} direction="column" >
              <Grid item xs={12}>
                <TextField fullWidth
                  autoComplete='off'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Nombre del rol"
                  value={values.roleName}
                  onChange={inputChange('roleName')} />
              </Grid>



              <Grid item xs={6}>
                <Button disabled={disabled} type="submit" variant="contained" color='primary'>{!props.role ? 'Crear' : 'Actualizar'}</Button>
              </Grid>
            </Grid>
          </form>
        </Paper>

      </Container>
    </Fragment>
  )
}

export default RoleForm