import React, { useState, Fragment, useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Button } from '@material-ui/core';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/DeleteOutlined';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import RestoreIcon from '@material-ui/icons/Restore';
import RoleForm from "./RoleForm";

import Tooltip from '@material-ui/core/Tooltip';
import TablePagination from '@material-ui/core/TablePagination';
import { getRoles, deleteRole, restoreRole } from '../../api/api';

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    iconWidth: {
        width: 10
    },
    paper: {
        padding: theme.spacing(2),
        marginTop: 10,
    },
    button: {
        margin: theme.spacing(1),
    },
    input: {
        display: 'none',
    },
    fab: {
        margin: theme.spacing(1),
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
    icon: {
        marginRight: theme.spacing(1),
        color: 'black'
    },
}))

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
})



const Role = () => {

    const classes = useStyles()

    const [roles, setRoles] = useState([])
    const [totalRoles, setTotalRoles] = useState(0)
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const [page, setPage] = useState(0);

    const [open, setOpen] = React.useState(false);
    const [openRestoreRole, setOpenRestoreRole] = React.useState(false);
    const [openEditRole, setOpenEditRole] = React.useState(false);
    const [openCreateRoleForm, setOpenCreateRoleForm] = React.useState(false);
    const [role, setRole] = useState({})


    useEffect(() => {
        let data = { start: 0, end: rowsPerPage }
        getRoles(data).then(response => {
            setRoles(response.roles);
            setTotalRoles(response.count);
        });
    }, []);

    const onOpenDeleteRole = role => () => {
        setRole(role)
        setOpen(true);
    }
    const onOpenRestoreRole = role => () => {
        setRole(role)
        setOpenRestoreRole(true);
    }
    const onCloseDeleteRole = () => {
        setOpen(false);
    }
    const onDeleteRole = () => {
        setOpen(false);

        deleteRole({ id: role._id }).then(response => {
            if (response.status === 'success') {
                const newRoles = roles.map((currentRole) => {
                    if (role._id !== currentRole._id)
                        return currentRole

                    currentRole.status = 'inactive'
                    return currentRole
                });
                setRoles(newRoles)
            }
        });
    }
    const onRestoreRole = () => {

        setOpenRestoreRole(false)

        restoreRole({ id: role._id }).then(response => {
            if (response.status === 'success') {
                const newRoles = roles.map((currentRoles, index) => {

                    if (role._id !== currentRoles._id)
                        return currentRoles

                    currentRoles.status = 'active'
                    return currentRoles
                })
                setRoles(newRoles)
            }
        });
    }
    const onEditRole = role => () => {
        setRole(role)
        setOpenEditRole(true)
    };
    const onCloseEditRole = () => {
        setOpenEditRole(false)
    };
    const onCloseRestoreRole = () => {
        setOpenRestoreRole(false)
    };
    const onCreateRole = () => {
        setOpenCreateRoleForm(true)
    }
    const onCloseRoleForm = () => {
        setOpenCreateRoleForm(false)
    }
    const passRoleCreate = role => {
        setRoles(roles => [...roles, role]);
        setOpenCreateRoleForm(false);
    }
    const passRoleEdit = role => {
        setOpenEditRole(false);
        const newRoles = roles.map((currentRoles) => {

            if (role._id !== currentRoles._id)
                return currentRoles

            currentRoles.name = role.name;
            return currentRoles
        })
        setRoles(newRoles)
    }
    const handleChangePage = (event, newPage) => {
        setPage(newPage);
        let data = { start: (newPage * rowsPerPage), end: rowsPerPage }
        getRoles(data).then(response => {
            setRoles(response.roles);
            setTotalRoles(response.count);
        });
    }
    const handleChangeRowsPerPage = event => {

        setRowsPerPage(+event.target.value);
        setPage(0);
        let data = { start: 0, end: event.target.value }
        getRoles(data).then(response => {
            setRoles(response.roles);
            setTotalRoles(response.count);
        });

    }



    return (
        <Fragment>
            <Paper className={classes.paper}>
                <Button variant="contained" color="primary" onClick={onCreateRole}>Crear rol</Button>

                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>Nombre</TableCell>
                            <TableCell colSpan={4}>Acciones</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {
                            roles.map(role => {
                                return (
                                    <TableRow key={role._id}>
                                        <TableCell>{role.name} </TableCell>

                                        <TableCell className={classes.iconWidth}>
                                            {
                                                !role.administrative ?
                                                    <Tooltip title="Editar" placement="top">
                                                        <EditIcon className={classes.icon} onClick={onEditRole(role)} />
                                                    </Tooltip>
                                                    : ""
                                            }
                                        </TableCell>
                                        <TableCell className={classes.iconWidth}>
                                            {
                                                !role.administrative ?
                                                    <Tooltip title="Eliminar" placement="top">
                                                        {
                                                            role.status === 'active' ?
                                                                <DeleteIcon className={classes.icon} onClick={onOpenDeleteRole(role)} />
                                                                :
                                                                <RestoreIcon className={classes.icon} onClick={onOpenRestoreRole(role)} />
                                                        }
                                                    </Tooltip>
                                                    : ""
                                            }
                                        </TableCell>

                                    </TableRow>)
                            }
                            )}
                    </TableBody>
                </Table>
                <TablePagination
                    rowsPerPageOptions={[10, 25, 100]}
                    component="div"
                    count={totalRoles}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={handleChangePage}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
            </Paper>
            <Dialog
                open={open}
                onClose={onCloseDeleteRole}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                    Alerta
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Esta seguro de eliminar este rol?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseDeleteRole} color="primary">
                        NO
                    </Button>
                    <Button onClick={onDeleteRole} color="primary">
                        SI
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                open={openRestoreRole}
                onClose={onCloseRestoreRole}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                    Alerta
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Esta seguro de Reactivar a este rol?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseRestoreRole} color="primary">
                        NO
                    </Button>
                    <Button onClick={onRestoreRole} color="primary">
                        SI
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog maxWidth="md" open={openEditRole} onClose={onCloseEditRole} TransitionComponent={Transition}>
                <DialogContent>
                    <RoleForm handleClose={onCloseEditRole} role={role} passRoleEdit={passRoleEdit} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseEditRole} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog open={openCreateRoleForm} onClose={onCloseRoleForm} >
                <DialogTitle id="form-dialog-title">Crear rol</DialogTitle>
                <DialogContent>
                    <RoleForm handleClose={onCloseRoleForm} passRoleCreate={passRoleCreate} />
                </DialogContent>
                <DialogActions>
                    <Button onClick={onCloseRoleForm} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>
        </Fragment>
    )
}

export default Role